.list-overflow-container {
    max-height: 545px;
    border: 1px #DDDDDD solid;
    border-radius: 4px;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 80%;
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
    
  }
  
  .list-overflow-container ul {
    margin: 0;
  }
  
  .list-overflow-container li {
    border-radius: 0px;
    border-left: none;
    border-right: none;
    padding-bottom: 50px;
    padding-top: 30px;
  }
  
  .list-overflow-container li * {
    margin: 0;
  }
  
  .list-overflow-container li a {
    color: white;
  }
  
  .list-overflow-container li:first-child {
    border-top: none;
  }
  
  .list-overflow-container li:last-child {
    border-bottom: none;
  }
  
  .image-description {
    padding-top: 30px;  
  }